.card {
  background: #fff;
  border-radius: 15px;
  height: 200px;
  line-height: 200px;
  margin: 0.75rem;
  position: relative;
  /* width: 325px; */
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:rgb(220, 194, 108);
  border-radius: 15px;
}
.card > .img-container > img {
  width: 75%;
  object-fit: contain;
  max-height: 100%;
}

#menu {
  max-width: 370px;
  position: relative;
}

#menu-text {
  width:"100%"; 
  position:"absolute"; 
  bottom: "10%"; 
  left: "50%"; 
  -webkit-transform: translate(-50%, 0); 
          transform: translate(-50%, 0); 
  text-align:"center"; 
  font-size:"32px"; 
  color: "#5a4827";
}

html, body {
  height: 100%;
  width: 100%;
  margin: 0;
}

#menu {
  height: 100%;
  width: 100%;
}

#menu p {
  margin: 0;
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

* {
  box-sizing: border-box;
  font-family: 'Acme', sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}


